function _randomdelay() {
    const ms = Math.random() * 1000;
    return new Promise(resolve => setTimeout(resolve, ms));
}

const jobs = {};

const ANNOTATIONS = [
    {x: 10, y: 20, width: 80, height: 120, class: "car"},
    {x: 20, y: 30, width: 120, height: 80, class: "dog"},
    {x: 30, y: 40, width: 180, height: 220, class: "cat"},
    {x: 40, y: 50, width: 220, height: 180, class: "pig"},
    {x: 50, y: 60, width: 220, height: 180, class: "hat"},
    {x: 60, y: 70, width: 220, height: 180, class: "yes"},
    {x: 70, y: 80, width: 220, height: 180, class: "not"},
    {x: 80, y: 90, width: 220, height: 180, class: "mug"}
]

function _buildStatus(images){
    const imageIds = Object.keys(images);
    const status = imageIds.map(imageId => {
        const modelNames = ["groundedSam", "clippedSem"];
        const models = modelNames.map(name => ({name, status: "waiting"}));
        return { image_id: imageId, models };
    });
    return status;
}

export default {
    async uploadImage(jobId, imageb64, classNames){
        await _randomdelay();
        const job = jobs[jobId] = jobs[jobId] || {};
        job.images = job.images || {};
        const imageId = Math.random().toString(16).substring(2);
        job.images[imageId] = { imageb64, classNames };
        return { image_id: imageId };
    },
    async getJobStatus(jobId, imageIds){
        await _randomdelay();
        const job = jobs[jobId];
        const status = job.status = job.status || _buildStatus(job.images);
        for (const image of status) {
            for(const model of image.models) {
                if (model.status === "waiting") {
                    if (Math.random() < 0.5) {
                        model.status = "done";
                        model.annotations = [
                            ANNOTATIONS[Math.floor(Math.random() * ANNOTATIONS.length)],
                            ANNOTATIONS[Math.floor(Math.random() * ANNOTATIONS.length)],
                        ];
                    }
                }
            }
        }
        return status;
    }
}
